import React from 'react';
import {Table} from 'react-bootstrap';
import { connect, useDispatch, useSelector } from 'react-redux';

function Cart(props){
    
    //index.js에서 감싼 reducer state 가져오는 훅 
    let state = useSelector( (state) => state)
    console.log(state)

    //dispatch 바로 쓰는법 !! 
    let dispatch = useDispatch()

    return(
        <div>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>상품번호</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>가격</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        //index.js에서 createStore에있는 state 가져오기
                        state.map((a,i)=>{
                            return (
                                <tr key={i}>
                                    <td>{ a.id }</td>
                                    <td>{ a.name }</td>
                                    <td>
                                        { a.quan }
                                        {/* dispatch는 데이터를 수정해달라는것
                                            누르면 type을 수량증가로 변경  */}
                                        <button onClick={()=>{ dispatch( { type: '수량증가' ,payload : a.id }) }}>+</button>
                                        <button onClick={()=>{ dispatch( { type: '수량감소' ,payload : a.id }) }}>-</button>
                                    </td>
                                    <td>{ a.price * a.quan }</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
            <div>총액 :100원
            </div>
        </div>
    )
}

// function state를props화(state){
//     return { 
//         state : state
//     }
// }

// export default connect(state를props화)(Cart)

export default Cart;