import React, {useContext, useEffect, useState, lazy, Suspense} from 'react';
import './App.scss'
import './reset.css'
import Beauty from './itemdata/beauty.js';
import Electronics from './itemdata/electronic.js';
import Fashion from './itemdata/fashion.js';
import Foods from './itemdata/foods.js';
import Sports from './itemdata/sports.js';
import Sale from './itemdata/sale.js';
import Detail from './Detail.js';
import Cart from './Cart.js';
import {Navbar ,Container ,NavDropdown ,Nav} from 'react-bootstrap';
import { Link, Route, Switch, useHistory} from 'react-router-dom';

//바로 로딩이 되지않고 Detail.js 가 필요할때 로딩이 되게 부담을 덜 준다 ~
// let Detail = lazy(()=>{ return import('./Detail.js') })

function App() {

  //카테고리 state---------------------------------------------------------
  let [BeautyItem, newBeauty] = useState(Beauty); 
  let [ElectronicsItem, newElectronicsItem] = useState(Electronics);
  let [FashionItem, newFashionItem] = useState(Fashion);
  let [FoodsItem, newFoodsItem] = useState(Foods);
  let [SportsItem, newSportsItem] = useState(Sports);
  //-----------------------------------------------------------------------

  let [saleItem, newsaleItem] = useState(Sale);//메인 state
  let [count,setCount] = useState(1); // 수량체크 state

  return (
    <div className="App">
      <div className = 'header'>
        <Navbar bg="light" expand="lg">
          <Container>
            <Navbar.Brand as={Link} to="/">BonoShop</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                <Nav.Link as={Link} to="/cart">Cart</Nav.Link>
                <NavDropdown title="Category" id="basic-nav-dropdown">
                  <NavDropdown.Item as={Link} to="/beauty" >Beauty</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/fashion">fashion</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/electronics">Electronics</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/foods">Foods</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/sports">Sports</NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
      </div>

      <Switch>
        <Route exact path="/">
          <MainBannerSlide></MainBannerSlide>

          <div className ="mainitemList sale">
            <h1>오늘의 특가 08월 10일 자정까지</h1>
            <ul>
              {/* sale 객체데이터에서 0~9번까지 */}
              {
                saleItem.map( (a,i)=>{
                  if( i <= 4){
                    return (
                      <SaleList count={count} setCount={setCount} saleItem={saleItem[i]} i={i}  key={i}/>
                    )
                  }
                })
              }
            </ul>
           
          </div>

          <div className ="eventbanner">
            <h2>지금 받을 수 있는 혜택</h2>
            <ul>
              <li className="eventImgs1 eventImgs">
                <h4>오늘의 혜택<br/>적립금 300만원</h4>
                <p>로그인만 하면 누구나!</p>
              </li>
              <li className="eventImgs2 eventImgs">
                <h4>등급별로 받는<br/>썸머니 혜택</h4>
                <p>매월 최대 5천원 쇼핑지원금</p>
              </li>
            </ul>
          </div>
          
          <div className="mainitemList best">
            <h1>지금, 제일 잘나가는 잇템이 모두 여기에!</h1>
            <ul>
              {/* sale 객체데이터에서 10~19번까지 */}
              {
                saleItem.map( (a,i=10)=>{
                  if( i>=10 && i<=14 ){
                    return (
                      <SaleList count={count} setCount={setCount} saleItem={saleItem[i]} i={i}  key={i}/>
                    )
                  }
                })
              }
            </ul>
          </div>
        </Route>

        <Route exact path="/detail/:id">
          <Detail BeautyItem={BeautyItem} saleItem={saleItem} count={count} setCount={setCount} ></Detail>
        </Route>

        <Route exact path="/beauty">
          <div className ="mainitemList sale">
              <h1>오늘의 특가 08월 10일 자정까지</h1>
              <ul className="row">
                {/* sale 객체데이터에서 0~9번까지 */}
                {
                  BeautyItem.map( (a,i)=>{
                    return (
                      <BeautyList BeautyItem={BeautyItem} count={count} setCount={setCount} 
                      BeautyItem={BeautyItem[i]} i={i}  key={i}/>
                    )
                  })
                }
              </ul>
            </div>
        </Route>

        <Route exact path="/fashion">
          <div className ="mainitemList sale">
            <h1>오늘의 특가 08월 10일 자정까지</h1>
            <ul className="row">
              {/* sale 객체데이터에서 0~9번까지 */}
              {
                FashionItem.map( (a,i)=>{
                  return (
                    <FashionList FashionItem={FashionItem} count={count} setCount={setCount} 
                    FashionItem={FashionItem[i]} i={i}  key={i}/>
                  )
                })
              }
            </ul>
          </div>
        </Route>

        <Route exact path="/electronics">
          <div className ="mainitemList sale">
            <h1>오늘의 특가 08월 10일 자정까지</h1>
            <ul className="row">
              {/* sale 객체데이터에서 0~9번까지 */}
              {
                ElectronicsItem.map( (a,i)=>{
                  return (
                    <ElectronicsList BeautyItem={ElectronicsItem} count={count} setCount={setCount} 
                    ElectronicsItem={ElectronicsItem[i]} i={i}  key={i}/>
                  )
                })
              }
            </ul>
          </div>
        </Route>

        <Route exact path="/foods">
          <div className ="mainitemList sale">
            <h1>오늘의 특가 08월 10일 자정까지</h1>
            <ul className="row">
              {/* sale 객체데이터에서 0~9번까지 */}
              {
                FoodsItem.map( (a,i)=>{
                  return (
                    <FoodsList FoodsItem={FoodsItem} count={count} setCount={setCount} 
                    FoodsItem={FoodsItem[i]} i={i}  key={i}/>
                  )
                })
              }
            </ul>
          </div>
        </Route>

        <Route exact path="/sports">
          <div className ="mainitemList sale">
            <h1>오늘의 특가 08월 10일 자정까지</h1>
            <ul className="row">
              {/* sale 객체데이터에서 0~9번까지 */}
              {
                SportsItem.map( (a,i)=>{
                  return (
                    <SportsList SportsItem={SportsItem} count={count} setCount={setCount} 
                    SportsItem={SportsItem[i]} i={i}  key={i}/>
                  )
                })
              }
            </ul>
          </div>
        </Route>

        <Route exact path="/cart">
          <Cart></Cart>
        </Route>
        
      </Switch>
    </div>
  );
}

//배너 슬라이드 개발예정중 !!
function MainBannerSlide(){

  return (
    <div className="mainBanner">
      <h1>풍성한 모발을 위한 첫걸음</h1>
      <p>두비,모발 홈케어가 답!</p>
    </div>
  )
}

//main페이지에 물건 리스트들 
function SaleList(props){

  //눌렀던것들 배열에 추가 ~ 
  let history = useHistory();

  return(
    //누르면 histiory에 id 값 추가 하고 추가한 id의 데이터를 받아와서 페이지에 표시 ! 
    <li className="mainItems"  onClick={()=>{
      history.push(`/detail/${props.saleItem.id}`)
      //count 1로 초기화
      props.setCount(1)
    }}>
      <div className="mainImg">
        <img src={`./img/main/sale${props.saleItem.id+1}.jpg`} alt="saleImg"></img>
      </div>
      <div className ="mainItemsInfo">
        <h2 className="mainItemsName">{props.saleItem.name}</h2>
        <p className="BeforePrice">{props.saleItem.beforePrice}원</p>
        <h4 className="NewPrice">{props.saleItem.price}원</h4>
      </div>
    </li>
  )
}

//---------------카테고리 물건 리스트 컴포넌트들 ---------------------
