import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {BrowserRouter} from 'react-router-dom';
import {connect, Provider} from 'react-redux';
import {createStore} from 'redux';


let 초기값 = [];

//데이터 수정할 곳!!
//수량증가라는 데이터 요청이 들어오면 quan 증가 ! 
function reducer(state = 초기값, 액션){
  if(액션.type === '항목추가'){

    //a.id와 액션.payload.id 값을 비교해서 같으면 몇번째에 있는지 found에 담았음
    let found = state.findIndex((a)=>{ return a.id === 액션.payload.id })

    //중복된것이 있다 !!
    if( found >= 0 ){

      let copy = [...state]; // 복사본 생성
      copy[found].quan++ //추출한 인덱스 번호.quan ++
      return copy

      // 중복된것이 없다!!
      //그냥 배열에 추가 ! 
    } else{

      let copy = [...state];
      copy.push(액션.payload);
      return copy
    }
  }

  else if( 액션.type === '수량증가'){
    let copy = [...state];//복사본 만들기
    copy[액션.payload].quan++;
    return copy

  }else if( 액션.type === '수량감소'){
    let copy = [...state];//복사본 만들기
    copy[액션.payload].quan--;
    console.log(액션)
    return copy

  }else{
    return state;
  }
}

let store = createStore(reducer)



ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
