import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import './Detail.scss'

function Detail(props){

    //id 값을 가져와서 ㄱ
    let { id } = useParams();
    let mainFoundId = props.saleItem.find( (a)=>{ return a.id == id });
    let cateFoundId = props.BeautyItem.find( (a)=>{ return a.id == id });
    let history = useHistory();
    console.log(mainFoundId)
    console.log(cateFoundId)
    
    return(
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <img src={`../img/main/sale${mainFoundId.id+1}.jpg`} alt="detailImg"></img>
                </div>
                <div className="col-md-6 mt-4 detailInfo">
                    <h6 className="pt-5">{mainFoundId.name}</h6>
                    <h3>{mainFoundId.itemCode}</h3>
                    <p>{mainFoundId.price}원</p>
                    
                    {/* 나중에 재고 숫자랑 비교해서 - 값이 나오면 주문이 안된다고
                    알람창 띄우기 그러고 없어지게 만들기 !  */}
                    <div className="price">
                        <CountControl count={props.count} setCount={props.setCount}/>
                        <p>총 금액({props.count}개) {mainFoundId.price * props.count} 원</p>
                    </div>
                    
                    <div className="mt-5 check">

                        {/* 주문하기 누를 때마다 누른 아이템의 정보를 전달해서 cart에 출력 */}
                        <button className="btn btn-danger orderBtn" onClick ={ ()=>{
                            props.dispatch({ 
                                type:'항목추가', 
                                payload : { id: mainFoundId.id, name:mainFoundId.name, quan : props.count, price: mainFoundId.price}
                            });
                        }}>주문하기</button>

                        {/* 뒤로가기 버튼 : '/' 페이지로 이동  */}
                        <button className="btn btn-danger backBtn" onClick = { () => {
                        history.push('/');
                        } }>뒤로가기</button>

                    </div>
                </div>
            </div>
        </div>
    )
}

function CountControl(props){
    return(
        //1(기본값) 일때  down 누르면 알람팝업뜸 !
        <div className="count">
            <button className="down" onClick={()=>{
                if(props.count > 1){
                    props.setCount(props.count-1)
                }else {
                    return (
                        alert('최소주문가능 개수는 1개 입니다.')
                    )
                }
            }}>-</button>
            <p>{props.count}</p>
            <button className="up" onClick={ ()=>{
                props.setCount(props.count+1 )
            }}>
            +</button>
        </div>
    )
}

function state를props화(state){
    return { state : state}
}

export default connect(state를props화)(Detail)